// src/app/dashboard/page.tsx

'use client';

import { useEffect, useState } from 'react';
// لم نعد نحتاج لاستيراد supabase هنا مباشرة لاستدعاء الوظائف
// import { supabase } from '@/utils/supabase/client'; 

export default function DashboardPage( ) {
  const [googleAdsData, setGoogleAdsData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // لغرض الاختبار، قم بتهيئة currentLoginCustomerId مباشرةً بمعرف العميل
  const [currentLoginCustomerId, setCurrentLoginCustomerId] = useState<string | null>('3271710441');

  useEffect(() => {
    async function fetchGoogleAdsData() {
      if (!currentLoginCustomerId) {
        setLoading(false);
        setError('معرف العميل غير متوفر.');
        return;
      }

      try {
        // استدعاء نقطة نهاية API الجديدة في Next.js بدلاً من وظيفة Supabase مباشرة
        const response = await fetch('/api/google-ads', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({ loginCustomerId: currentLoginCustomerId }),
        });

        if (!response.ok) {
          const errorData = await response.json();
          setError(errorData.error || 'Failed to fetch data from API route');
        } else {
          const data = await response.json();
          setGoogleAdsData(data);
        }
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }

    if (currentLoginCustomerId) {
      fetchGoogleAdsData();
    }
  }, [currentLoginCustomerId]);

  if (loading) return <p>جاري تحميل بيانات Google Ads...</p>;
  if (error) return <p>حدث خطأ: {error}</p>;

  return (
    <div>
      <h1>بيانات Google Ads</h1>
      <pre>{JSON.stringify(googleAdsData, null, 2)}</pre>
    </div>
  );
}
